import * as Sentry from '@sentry/browser';

import config from '../config';
import Session from './session';

class ErrorTracker {
  static setup() {
    if (config.isProduction) {
      Sentry.init({
        dsn: config.sentryDsn, environment: config.env
      });
      Sentry.setExtra('server', config.apiUrl);
    }
  }

  static async capture(error, receivedOpts = {}) {
    if (error) {
      if (config.isProduction) {
        const viewer = this.getViewerData();
        Sentry.setUser(viewer);
        Object.keys(receivedOpts).forEach(k => {
          Sentry.setExtra(k, receivedOpts[k]);
        });
        Sentry.captureException(error);
      } else {
        console.warn(error);
      }
    }
  }

  static getViewerData() {
    try {
      const user = Session.getUserData();
      if (user && user.id) {
        const viewer = user.member || user;
        const { id, email } = viewer;
        return { id, email };
      }
      return {};
    } catch {
      return {};
    }
  }
}

export default ErrorTracker;
